	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"During the evolution of the ideas behind Continuous Delivery, many of us came to the conclusion that having branches for features was not a good idea, and resulted in some fairly problematic issues. This was contentious at the time, with lots of discussion around whether or not feature toggles or feature branching was the right way forward.\n\nRoll on several years, and through Git and GitHub, branches are everywhere. Is this a problem?\n\nThis talk re-examines the role of feature branches and feature toggles, and looks at them in the context of new research and technology to try and distill down some sensible lessons in a post-GitHub, but hopefully not post-factual, world.","meta":{"duration":"PT49M58S"},"presenter":{"name":"Sam Newman"},"source":{"id":"lqRQYEHAtpk","provider":"youtube"},"slug":"feature-branches-and-toggles-in-a-post-github-world","tag":["GOTO Conference","Extreme Programming","Continuous Delivery","Trunk Based Development","Feature Branches"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/lqRQYEHAtpk/default.jpg","height":90,"width":120}},"title":"Feature Branches and Toggles in a Post-GitHub World"}}]}},"pathContext":{"speaker":"Sam Newman"}}

